<template>
    <div class="ok">
    
        <Head :agood='true' :bgood='bgood' :cgood='3'></Head>
      
      <div id="content">
        <div class="login-s">
          <div>
            <input type="text" placeholder="账号"/>
          </div>
          <div>
            <input type="password" placeholder="旧密码"/>
          </div>
          <div>
            <input type="password" placeholder="请输入新密码"/>
          </div>
          <div>
            <input type="password" placeholder="请确认密码"/>
          </div>
          <div>
            <input type="text" placeholder="验证码" maxlength="4" id="text"/>
            <div class="number" id="number"><img :src="numcode"></div>
           
            <button @click="one">看不清换一张</button>
              
           
          </div>
        </div>
        <!-- 按钮 -->
        <div class="logina">
          <input type="button" value="确认修改"/>
        </div>
       </div>
    </div>
</template>
<script>
import axios from 'axios'
axios.defaults.withCredentials=true;// 让 axios 请求携带 cookie
import Head from '@/components/head/Head'
import { MessageBox } from 'mint-ui'
export default {
  name: 'Login',
  components:{Head},
  data(){
    return{
      bgood:'重置密码',
    //   user:'', // 账号
    //   password:'', // 密码 
    //   code:'',  // 验证码
      numcode:'',// 随机数
      show:false
    }
  },
  methods:{
    one(){
      axios.post("https://elm.cangdu.org/v1/captchas").then((res)=>{
     
      if(res.data.code!=''){
        // debugger;
        this.numcode=res.data.code
         console.log(res.data.code)
      }
    })
  }
  
},
  
  mounted(){
    this.one();
  },  
}
</script>

<style lang="scss">


input,p{
  padding: 0;
  margin: 0;
  font-size: .24rem;
}
.login-s{
  background: white;
  margin-top: 1.2rem;
  
}
.login-s>div{
  
    width: 100%;
    height: .96rem;;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #f1f1f1;
    padding: 0;
    margin: 0;
    
  
}
.login-s>div>input{
      border: none;
      margin-left: .3rem;
      width: 3.84rem;
      height: .39rem;
    }

//
button{
  width: 1.1rem;
  height: .8rem;
  font-size: .24rem;
  border: none;
  background: none;
}
.logina{
  width: 95%;
  height: 1rem;
  margin: 0 auto;
  font-size: .2rem;
  margin-top: .2rem;
  input{
    width: 100%;
    height: 100%;
    background: #4cd964;
    border: none;
    border-radius: 0.05rem;
    font-size: .4rem;
    color: white;
  }
}
.number{
  width: .9rem;
  height: .5rem;
  font-size: .44rem;
  text-align: center;
  line-height: .44rem;
}
</style>



